"use client"

import React from "react"
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  ScatterChart,
  Scatter,
  CartesianGrid,
  ResponsiveContainer,
  RadarChart,
  PolarGrid,
  PolarAngleAxis,
  PolarRadiusAxis,
  Radar,
} from "recharts"

interface CombinedChartsProps {
  barData: {
    name?: string
    assessment_score: number
    [key: string]: any
  }[]
  scatterData: {
    name: string
    attention: number
    assessment_score: number
  }[]
  radarProfiles: {
    comprehension: number
    attention: number
    focus: number
    retention: number
    engagement_time: number
    assessment_score: number
  }[]
}

export default function CombinedCharts({
  barData,
  scatterData,
  radarProfiles,
}: CombinedChartsProps) {
  // Transform Bar data
  const chartData = barData.map((s, idx) => ({
    name: s.name || `Student ${idx + 1}`,
    value: s.assessment_score ?? 0,
  }))

  // Transform Scatter data
  const points = scatterData.map((d) => ({
    x: d.attention,
    y: d.assessment_score,
    name: d.name,
  }))

  // Transform Radar data
  const radarData = radarProfiles.map((p, idx) => ({
    student: `S${idx + 1}`,
    Comprehension: p.comprehension,
    Attention: p.attention,
    Focus: p.focus,
    Retention: p.retention,
    Engagement: p.engagement_time,
  }))

  return (
    <div className="p-6">
      {/* Heading */}
      <h2 className="text-2xl font-bold mb-6">Charts</h2>

      {/* Charts Grid */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Bar Chart */}
        <div className="w-full h-80 bg-white rounded-2xl shadow p-4">
          <ResponsiveContainer>
            <BarChart data={chartData}>
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip />
              <Legend />
              <Bar dataKey="value" name="Assessment Score" fill="#4f46e5" />
            </BarChart>
          </ResponsiveContainer>
        </div>

        {/* Scatter Chart */}
        <div className="w-full h-80 bg-white rounded-2xl shadow p-4">
          <ResponsiveContainer>
            <ScatterChart margin={{ top: 20, right: 20, bottom: 10, left: 0 }}>
              <CartesianGrid />
              <XAxis type="number" dataKey="x" name="Attention" />
              <YAxis type="number" dataKey="y" name="Assessment" />
              <Tooltip cursor={{ strokeDasharray: "3 3" }} />
              <Scatter data={points} fill="#8884d8" />
            </ScatterChart>
          </ResponsiveContainer>
        </div>

        {/* Radar Chart */}
        <div className="w-full h-80 bg-white rounded-2xl shadow p-4 flex items-center justify-center">
          <RadarChart outerRadius={100} width={350} height={300} data={radarData}>
            <PolarGrid />
            <PolarAngleAxis dataKey="student" />
            <PolarRadiusAxis />
            <Radar
              name="Comprehension"
              dataKey="Comprehension"
              stroke="#8884d8"
              fill="#8884d8"
              fillOpacity={0.6}
            />
            <Radar
              name="Attention"
              dataKey="Attention"
              stroke="#82ca9d"
              fill="#82ca9d"
              fillOpacity={0.6}
            />
            <Radar
              name="Focus"
              dataKey="Focus"
              stroke="#ffc658"
              fill="#ffc658"
              fillOpacity={0.6}
            />
            <Radar
              name="Retention"
              dataKey="Retention"
              stroke="#ff6f91"
              fill="#ff6f91"
              fillOpacity={0.6}
            />
            <Radar
              name="Engagement"
              dataKey="Engagement"
              stroke="#00bfff"
              fill="#00bfff"
              fillOpacity={0.6}
            />
            <Tooltip />
            <Legend verticalAlign="top" />
          </RadarChart>
        </div>
      </div>
    </div>
  )
}
